import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';

import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { HttpModule } from '@angular/http';
import { FormsModule, ReactiveFormsModule} from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { RouterModule, Routes } from '@angular/router';

import {ChartsModule} from 'ng2-charts';

import { MaterialModule, KendoModule } from './shared/index';
import { GridService, CategoriesService} from './service/index';
import { AuthService} from './authentication/services/auth.service';
import { AuthGuardService} from './authentication/services/auth-guard.service';
import { SpeechRecognitionService} from './service/speech-recognition.service';


import { TreeviewModule } from '../../node_modules/ngx-treeview/src/index';

import { AuthModule} from './authentication/auth.module';

import { AppComponent } from './app.component';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { MainComponent } from './main/main.component';
import { AsideComponent } from './aside/aside.component';
import { HomeComponent } from './home/home.component';
import { ChartComponent } from './chart/chart.component';
import { UploadComponent } from './upload/upload.component';
import { KendoGridComponent } from './kendo-grid/kendo-grid.component';
import { KendoTreeviewComponent } from './kendo-treeview/kendo-treeview.component';
import { JsInNgComponent } from './js-in-ng/js-in-ng.component';
import { NgxTreeviewComponent } from './ngx-treeview/ngx-treeview.component';
import { MatDialogueComponent } from './mat-dialogue/mat-dialogue.component';
import { RegisterFormComponent } from './register-form/register-form.component';
import { WebSpeechApiComponent } from './web-speech-api/web-speech-api.component';


const appRoutes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: '', redirectTo: 'home', pathMatch: 'full' },
  { path: 'main', component: MainComponent},
  { path: 'chart', component: ChartComponent},
  { path: 'upload', component: UploadComponent},
  { path: 'kendo-grid', component: KendoGridComponent},
  { path: 'kendo-treeview', component: KendoTreeviewComponent},
  { path: 'js-in-ng', component: JsInNgComponent},
  { path: 'ngx-treeview', component: NgxTreeviewComponent},
  { path: 'mat-dialogue', component: MatDialogueComponent},
  { path: 'register-form', component: RegisterFormComponent},
  { path: 'web-speech-api', component: WebSpeechApiComponent},
  { path: 'auth', loadChildren: './authentication/auth.module#AuthModule'}
]

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    FooterComponent,
    MainComponent,
    AsideComponent,
    HomeComponent,
    ChartComponent,
    UploadComponent,
    KendoGridComponent,
    KendoTreeviewComponent,
    JsInNgComponent,
    NgxTreeviewComponent,
    MatDialogueComponent,
    RegisterFormComponent,
    WebSpeechApiComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    RouterModule.forRoot(appRoutes),
    ChartsModule,
    MaterialModule, KendoModule,
    TreeviewModule
  ],
  providers: [GridService, CategoriesService, SpeechRecognitionService, AuthService, AuthGuardService],
  bootstrap: [AppComponent]
})
export class AppModule { }



