import { Component, OnInit, OnDestroy } from '@angular/core';
import { SpeechRecognitionService } from '../service/speech-recognition.service';


@Component({
  selector: 'app-web-speech-api',
  templateUrl: './web-speech-api.component.html',
  styleUrls: ['./web-speech-api.component.css']
})
export class WebSpeechApiComponent implements OnInit, OnDestroy {
	showSearchButton: boolean;
    speechData: string;

    constructor(private speechRecognitionService: SpeechRecognitionService) {
        this.showSearchButton = true;
        this.speechData = "";
    }

    ngOnInit() {
        console.log("hello")
    }

    ngOnDestroy() {
        this.speechRecognitionService.DestroySpeechObject();
    }

    activateSpeechSearchMovie(): void {
        this.showSearchButton = false;

        this.speechRecognitionService.record()
            .subscribe(
            //listener
            (value) => {
                this.speechData = value;
                console.log(value);
            },
            //errror
            (err) => {
                console.log(err);
                if (err.error == "no-speech") {
                    console.log("--restatring service--");
                    this.activateSpeechSearchMovie();
                }
            },
            //completion
            () => {
                this.showSearchButton = true;
                console.log("--complete--");
                this.activateSpeechSearchMovie();
            });
    }
}



