import { Component, OnInit } from '@angular/core';
import {MatDialog} from '@angular/material';

import{KendoGridComponent} from '../kendo-grid/kendo-grid.component';

@Component({
  selector: 'app-mat-dialogue',
  templateUrl: './mat-dialogue.component.html',
  styleUrls: ['./mat-dialogue.component.css']
})
export class MatDialogueComponent implements OnInit {

  constructor(public dialog: MatDialog) { }
  openDialog(): void {
    let dialogRef = this.dialog.open(KendoGridComponent, {
      width: '700px',
      height:'80%'
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');
    });
  }

  ngOnInit() {
  }

}



